
/*****************************************/


pre code.hljs {
	display: block;
	overflow-x: auto;
	padding: 1em;
	color: #24292F;
	font-size: 0.875rem;
	background: #F8F9Fa;
	border: 1px solid #D0D7DE;
	-moz-border-radius: 0.1875rem;
	-webkit-border-radius: 0.1875rem;
	border-radius: 0.1875rem;
	font-family: $font-family-monospace;
  }
  
  code.hljs {
	padding: 3px 5px;
  }
  
  /*
	Theme: GitHub
	Description: Light theme as seen on github.com
	Author: github.com
	Maintainer: @Hirse
	Updated: 2021-05-15
  
	Outdated base version: https://github.com/primer/github-syntax-light
	Current colors taken from GitHub's CSS
  */
  
  .hljs {
	color: #24292e;
	background: #fff;
  }
  
  .hljs-doctag, .hljs-keyword, .hljs-meta .hljs-keyword, .hljs-template-tag, .hljs-template-variable, .hljs-type, .hljs-variable.language_ {
	color: #CF3847;
  }
  
  .hljs-title {
	color: #6f42c1;
  
	&.class_ {
	  color: #6f42c1;
  
	  &.inherited__ {
		color: #6f42c1;
	  }
	}
  
	&.function_ {
	  color: #6f42c1;
	}
  }
  
  .hljs-attr, .hljs-attribute, .hljs-literal, .hljs-meta, .hljs-number, .hljs-operator, .hljs-selector-attr, .hljs-selector-class, .hljs-selector-id, .hljs-variable {
	color: #005cc5;
  }
  
  .hljs-meta .hljs-string, .hljs-regexp, .hljs-string {
	color: #032f62;
  }
  
  .hljs-built_in, .hljs-symbol {
	color: #BD5008;
  }
  
  .hljs-code, .hljs-comment, .hljs-formula {
	color: #68717A;
  }
  
  .hljs-name, .hljs-quote, .hljs-selector-pseudo, .hljs-selector-tag {
	color: #218237;
  }
  
  .hljs-subst {
	color: #24292e;
  }
  
  .hljs-section {
	color: #005cc5;
	font-weight: 700;
  }
  
  .hljs-bullet {
	color: #735c0f;
  }
  
  .hljs-emphasis {
	color: #24292e;
	font-style: italic;
  }
  
  .hljs-strong {
	color: #24292e;
	font-weight: 700;
  }
  
  .hljs-addition {
	color: #218237;
	background-color: #f0fff4;
  }
  
  .hljs-deletion {
	color: #b31d28;
	background-color: #ffeef0;
  }


@media (prefers-color-scheme: dark) {


  pre code.hljs {
	color: #C9D1D9;
	background: #0D1117;
	border: 1px solid #30363D;
  }
  
  code.hljs {
	padding: 3px 5px;
  }
  
  /*
	Theme: GitHub Dark
	Description: Dark theme as seen on github.com
	Author: github.com
	Maintainer: @Hirse
	Updated: 2021-05-15
  
	Outdated base version: https://github.com/primer/github-syntax-dark
	Current colors taken from GitHub's CSS
  */

  .hljs {
	color: #c9d1d9;
	background: #0d1117;
  }
  
  .hljs-doctag, .hljs-keyword, .hljs-meta .hljs-keyword, .hljs-template-tag, .hljs-template-variable, .hljs-type, .hljs-variable.language_ {
	color: #ff7b72;
  }
  
  .hljs-title {
	color: #d2a8ff;
  
	&.class_ {
	  color: #d2a8ff;
  
	  &.inherited__ {
		color: #d2a8ff;
	  }
	}
  
	&.function_ {
	  color: #d2a8ff;
	}
  }
  
  .hljs-attr, .hljs-attribute, .hljs-literal, .hljs-meta, .hljs-number, .hljs-operator, .hljs-selector-attr, .hljs-selector-class, .hljs-selector-id, .hljs-variable {
	color: #79c0ff;
  }
  
  .hljs-meta .hljs-string, .hljs-regexp, .hljs-string {
	color: #a5d6ff;
  }
  
  .hljs-built_in, .hljs-symbol {
	color: #ffa657;
  }
  
  .hljs-code, .hljs-comment, .hljs-formula {
	color: #8b949e;
  }
  
  .hljs-name, .hljs-quote, .hljs-selector-pseudo, .hljs-selector-tag {
	color: #7ee787;
  }
  
  .hljs-subst {
	color: #c9d1d9;
  }
  
  .hljs-section {
	color: #2077FA;
	font-weight: 700;
  }
  
  .hljs-bullet {
	color: #f2cc60;
  }
  
  .hljs-emphasis {
	color: #c9d1d9;
	font-style: italic;
  }
  
  .hljs-strong {
	color: #c9d1d9;
	font-weight: 700;
  }
  
  .hljs-addition {
	color: #aff5b4;
	background-color: #033a16;
  }
  
  .hljs-deletion {
	color: #ffdcd7;
	background-color: #67060c;
  }
}